import {
  mapState,
  mapMutations
} from 'vuex'
import TodoHeader from "../../components/todoHeader.vue";
import swiperAction from '../../components/swiperAction.vue'
import swiperActionList from '../../components/swiperActionList.vue'
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
export default {
  components: {
    TodoHeader,
    swiperAction,
    swiperActionList,
    uniSwipeAction,
    uniSwipeActionItem
  },
  data() {
    return {
      todoList: [
        {
          createTime: 123,
          content: '今晚打老虎',
          checked: false,
          deleted: false
        },
        {
          createTime: 12345,
          content: '今晚打麻将a啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊阿啊啊啊啊啊啊啊',
          checked: false,
          deleted: false
        }
      ],
      showTextarea: false,
      actionVisible: false,
      height: 0,
      textValue: '',
      isAddingTodo: false,
      options: [{
      	text: '取消置顶'
      }],
    };
  },
  computed: {
    ...mapState(['tab', 'isLogin']), // 0 代办，1 已完成 2 全部
    listData() {
      const list = JSON.parse(JSON.stringify(this.todoList))

      // 代办
      if (this.tab === 0) {
        console.log("todo 0")
        return list.filter(item => !item.checked)
      }
      // 点击已完成
      if (this.tab === 1) {
        console.log("todo 1")
        return list.filter(item => item.checked)
      }
      // 点击全部
      if (this.tab === 2) {
        console.log("todo 2")
        return list;
      }
    }
  },
  methods: {
    ...mapMutations(['switchTab']),
    addTodo() {
      this.showTextarea = true
      this.isAddingTodo = true
    },
    handleConfirm(e) {
      this.showTextarea = false
      if (this.isAddingTodo) { // 说明这次的textarea是添加todo
        const newTodo = {
          createTime: Date.now(),
          content: this.textValue,
          checked: false,
          deleted: false
        }
        this.todoList.push(newTodo)
        this.textValue = ''
        this.isAddingTodo = false
        this.$http.post('/todo', newTodo, {
          header: {
            Cookie: `userId=${uni.getStorageSync('userId')}`
          }
        }).then(res => {
          if (res.data === 'Insert Success') {
            console.log('插入成功')
            return
          }
          if (res.data === 'Insert Error') {
            uni.showToast({
              title: '服务器插入失败'
            })
          }
        })
        return
      }
      // 下面的代码是编辑todo
      this.$emit('hideTextarea', this.textValue)
      this.textValue = ''
    },
    handleInput(e) {
      this.textValue = e.detail.value
    },
    clickHide() {
      this.showTextarea = false
      this.textValue = ''
    },
    handleDeleteTodo({ todoCreateTime }) {
      const index = this.todoList.findIndex(item => item.createTime === todoCreateTime)
      this.todoList.splice(index, 1)
      this.$http.delete('/todo', {
        createTime: todoCreateTime
      }, {
        header: {
          Cookie: `userId=${uni.getStorageSync('userId')}`
        }
      }).then(res => {
        if (res.data === 'Delete Error') {
          uni.showToast({
            title: '服务器删除失败',
          })
          return
        }
        if (res.data === 'Delete Success') {
          console.log('删除成功')
        }
      })
    },
    handleEditTodo({todoCreateTime, todoContent}) {
      this.textValue = todoContent
      this.showTextarea = true
      this.$once('hideTextarea', textValue => {
        const index = this.todoList.findIndex(item => item.createTime === todoCreateTime)
        const newTodo = {
          ...this.todoList[index],
          content: textValue
        }
        this.$set(this.todoList, index, newTodo)
        this.textValue = ''
        this.$http.put('/todo', newTodo, {
          header: {
            Cookie: `userId=${uni.getStorageSync('userId')}`
          }
        }).then(res => {
          if (res.data === 'Update Error') {
            uni.showToast({
              title: '服务器更新失败',
            })
            return
          }
          if (res.data === 'Update Success') {
            console.log('更新成功')
          }
        })
        console.log('edit todo之后', this.todoList)
      })
    },
    finishTodo(todoCreateTime) {
      const index = this.todoList.findIndex(item => item.createTime === todoCreateTime)
      const newTodo = {
        ...this.todoList[index],
        checked: !this.todoList[index].checked
      }
      this.$set(this.todoList, index, newTodo)
      this.$http.put('/todo', newTodo, {
        header: {
          Cookie: `userId=${uni.getStorageSync('userId')}`
        }
      }).then(res => {
        if (res.data === 'Update Error') {
          uni.showToast({
            title: '服务器更新失败',
          })
          return
        }
        if (res.data === 'Update Success') {
          console.log('更新成功')
        }
      })
    }
  },
  onLoad() {
    if (this.isLogin) {
      this.$http.get('/todo', {
        header: {
          Cookie: `userId=${uni.getStorageSync('userId')}`
        }
      }).then(res => {
        console.log(res.data)
        // res.data.forEach(this.todoList.push)
        res.data.forEach(item => this.todoList.push(item))
      })
    }
    uni.$once('loginFinished', () => {
      this.$http.get('/todo', {
        header: {
          Cookie: `userId=${uni.getStorageSync('userId')}`
        }
      }).then(res => {
        console.log(res.data)
        res.data.forEach(item => this.todoList.push(item))
      })
    })
  }
};
